.wrap {
  height: 100vh;

  & > .chatItem {
    height: 35px;
    padding: 15px;

    display: grid;
    align-items: center;
    cursor: pointer;
  }

  & > .chatItem:hover {
    background-color: #f0f2f5;
  }

  & > .addBtn {
    width: 100%;
    height: 35px;
    display: grid;
    place-items: center;

    & > button {
      width: 25px;
      height: 25px;
      border-radius: 50%;
      border: none;
      background-color: #00a884;
      color: #ffffff;
      font-size: 20px;
      cursor: pointer;
    }
  }

  & > .addChatContainer {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr auto;

    & > input {
      height: 25px;
      text-indent: 15px;
    }

    & > button {
      cursor: pointer;
      border: none;
      padding: 5px;
      background-color: #00a884;
      border-radius: 5px;
    }
  }
}
